import { Component, OnInit } from '@angular/core';
import {IdService} from "../../services/id.service";
import {ActivatedRoute, Router} from "@angular/router";
import {LoaderService} from "../../services/loader.service";
import {Storage} from "@ionic/storage";
import {SermonService} from "../../services/sermon.service";
import {EventsService} from "../../services/events.service";
import {ApiService} from "../../services/api.service";
import {ToastService} from "../../services/toast.service";
import {Location, formatDate} from "@angular/common";
import {CommonService} from "../../services/common.service";
import {EventProviderService} from "../../services/event-provider.service";
import * as moment from 'moment';
import {Platform} from "@ionic/angular";
import {Device} from "@ionic-native/device/ngx";
import {AnalyticsService} from "../../services/analytics.service";
import {TranslateService} from "@ngx-translate/core";
import { AppService } from '../app.service';

@Component({
  selector: 'app-custom-post',
  templateUrl: './custom-post.page.html',
  styleUrls: ['./custom-post.page.scss'],
})
export class CustomPostPage implements OnInit {
  allNotes: any = [];
  post_url: any;
  banner_image: any;
  banner_desc: any;
  banner_title: any;
  enable_home: any;
  page_id: any;
  post_category_url: any;
  is_external_url: boolean = false;
  start_time_analytics: any;
  page_name: any;
  custom_name: any;
  user: any;
  theme: any;
    alertMessage: any;
  page_in_home_tabs: any = false;
  image_url:any;
  is_wordpress_enable: boolean;
  page: number = 0;
  deviceType: string = '';

  constructor(
      private idService: IdService,
      private route: ActivatedRoute,
      private loader: LoaderService,
      private storage: Storage,
      private sermonsService: SermonService,
      private router: Router,
      private eventsService: EventsService,
      private apiService: ApiService,
      private toast: ToastService,
      public appService: AppService,
      private location: Location,
      private common: CommonService,
      private event_provider: EventProviderService,
      private analytics: AnalyticsService,
      private platform: Platform,
      private device: Device,
      private translate: TranslateService,
  ) {
      // get translated alert message
      let interval = setInterval(() => {
          this.translate.get('ALERTS').subscribe( (res: any) => {
              this.alertMessage = res;
              clearInterval(interval);
          });
      }, 2000)

    // chekcing if page is opened in home tabs or simple
    let current_url = new URL(window.location.href);
    let page_href = current_url.pathname;
    if (page_href.includes('home-tabs')){
      this.page_in_home_tabs = true;
    } else {
      this.page_in_home_tabs = false;
    }
  }

  ngOnInit() {
    // assigning data from URL Params
    // this.page_id = this.route.snapshot.paramMap.get('id');
    this.page_id = this.route.snapshot.paramMap.get('page_id');

    this.getTheme();

    // event subscription, runs when event fired from common service to update if HOME Page is enabled or not
    this.event_provider.isHomeEnable.subscribe(value => {              // listening event
      this.enable_home = value.value;
    });
    if (!this.enable_home){
      // updating home page status from variable set in common service
      // "enable_home" variable is used to switch ON or OFF the back button on Mobile View
      // If the HOME page is disabled and THIS page is selected as first landing page, there should be no BACK button
      this.enable_home = this.common.is_home_enable;
    }

    this.getUser();
  }

  getTheme() {
    // getting theme from local storage
    // this.storage.get('theme').then(theme => {
      if(localStorage.getItem('church_data')){
        let theme = JSON.parse(localStorage.getItem('church_data')).church;
  
        // getting app_pages
        const body = JSON.stringify({id: theme.ch_id});
        this.event_provider.getChurchData.subscribe(async (res: any) => {
          if((Object.keys(res).length === 0)){
            return;
          }
          this.theme = res.church;
  
          if (res.apppages.length > 0) {
            let custom_post_page = res.apppages.find(x => x.id === this.page_id);
  
            if (custom_post_page){
              this.banner_image = custom_post_page.website_banner;
              this.banner_desc = custom_post_page.banner_description;
              this.banner_title = custom_post_page.banner_title;
              this.post_url = custom_post_page.url;
              this.post_category_url = custom_post_page.custom_category_url;
              this.page_name = custom_post_page.name;
              this.custom_name = custom_post_page.title;
            }

            if (this.post_url) {
              this.is_wordpress_enable = false;
              this.getNotes(this.post_url);
            } else if (this.post_category_url && this.post_category_url.indexOf("https://") != -1) {
              this.is_wordpress_enable = false;
              this.getCategoryNotes(this.post_category_url);
            } else if (this.post_category_url && this.post_category_url.indexOf("https://") == -1)
              if(res.is_wordpress_enable === '1') {
                this.is_wordpress_enable = true;
                this.getDBPosts();
              }



            // if(res.is_wordpress_enable === '1'){
            //   this.is_wordpress_enable = true;
            //   if (this.post_url) {
            //     this.getNotes(this.post_url);
            //   } else if (this.post_category_url) {
            //    this.getCategoryNotes(this.post_category_url);
            //   }{
            //     this.getDBPosts();
            //   }
              
            // } else {
            //   this.is_wordpress_enable = false;
            //   if (this.post_url) {
            //     this.getNotes(this.post_url);
            //   } else if (this.post_category_url) {
            //     this.getCategoryNotes(this.post_category_url);
            //   }
            // }
          }
        });
      } else {
        this.storage.get('theme').then(theme => {
  
        // getting app_pages
        const body = JSON.stringify({id: theme.ch_id});
        this.common.getTheme(body).subscribe((res: any) => {
        // this.event_provider.getChurchData.subscribe(async (res: any) => {
  
          this.theme = res.church;
  
          if (res.apppages.length > 0) {
            let custom_post_page = res.apppages.find(x => x.id === this.page_id);
  
            if (custom_post_page){
              this.banner_image = custom_post_page.website_banner;
              this.banner_desc = custom_post_page.banner_description;
              this.banner_title = custom_post_page.banner_title;
              this.post_url = custom_post_page.url;
              this.post_category_url = custom_post_page.custom_category_url;
              this.page_name = custom_post_page.name;
              this.custom_name = custom_post_page.title;
            }
  
            if (this.post_url) {
              this.getNotes(this.post_url);
            } else if (this.post_category_url) {
              this.getCategoryNotes(this.post_category_url);
            }
          }
          });
        });
        
      }
  }

  getDBPosts(){


  this.loader.presentLoading().then( () => {
    this.is_external_url = true;

    let self = this;
    let body = {
      'church_id': this.theme.ch_id,
      'page_id': this.page_id,
      'page': this.page + 1
    };
    setTimeout(() => {
      this.loader.stopLoading();
    }, 2000);
    this.sermonsService.getCMSSermonPosts(body).subscribe((res: any) => {
      // ======= parsing xml into JsSON format =============================
      // let parseString = require('xml2js').parseString;
      // parseString(res, function(err, result) {
      //   self.allNotes = result.rss.channel[0].item;
      //   self.loader.stopLoading();

      // });
      // ======= parsing xml into JsSON format =============================

      self.allNotes = res.notes;
      self.allNotes.map( x => {
        x['pubDate'] = x.created_at;
      });
      this.loader.stopLoading();
    }, (err: any) => {
      let message = err.message;
      this.loader.stopLoading();
      this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
    });
    this.loader.stopLoading();
  });
  this.loader.stopLoading();
  }

  getNotes(url) {
    this.loader.presentLoading().then( () => {
    this.is_external_url = true;

    let self = this;
    setTimeout(() => {
      this.loader.stopLoading();
    }, 2000);
    // fetch sermon notes
    this.sermonsService.getSermonNotes(url, this.page_id).subscribe((res: any) => {
      // ======= parsing xml into JsSON format =============================
      // let parseString = require('xml2js').parseString;
      // parseString(res, function(err, result) {
      //   self.allNotes = result.rss.channel[0].item;
      //   self.loader.stopLoading();

      // });
      // ======= parsing xml into JsSON format =============================

      self.allNotes = res.result.channel.item;
      self.allNotes.map((res)=>{
        if(res.description && JSON.stringify(res.description) != '{}') {
          if (res.description.includes("<img")) {
            res.image_url = (res.description.match(/src=(.+?[\.jpg|\.gif|\.png]")/)[1]).slice(1, -1);
            if (res.image_url.includes("150x150")) {
              if (res.image_url.includes("-150x150")) {
                res.image_url = res.image_url.replace("-150x150", '');
              } else {
                res.image_url = res.image_url.replace("150x150", '');
              }
            } else if (res.image_url.includes("300x300")) {
              if (res.image_url.includes("-300x300")) {
                res.image_url = res.image_url.replace("-300x300", '');
              } else {
                res.image_url = res.image_url.replace("300x300", '');
              }
            }

            if( res.image_url !== "" && res.image_url !== null && res.image_url.includes("s3.wasabisys.com") && formatDate(res.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29' ){
              let abc =res.image_url.split("/");
              //  let img_name = abc[abc.length-1];
                let folder_name = abc[abc.length-2];
              if(this.platform.is("mobile")){
                  this.deviceType = 'radiobase_mobile';
              }else if(this.platform.is("tablet")){
                  this.deviceType = 'radiobase_tablet';
              }else{
                  this.deviceType = folder_name;
              }

              let new_link = res.image_url.replace(folder_name, this.deviceType);
              res.image_url = new_link; 
             // console.log('background is: ',card.background,'new link is: ', new_link);
          }
          }
        }
      });






      this.loader.stopLoading();
    }, (err: any) => {
      let message = err.message;
      this.loader.stopLoading();
      this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
    });
    })
  }

  getCategoryNotes(url) {
    this.loader.presentLoading().then( () => {

      let self = this;
      setTimeout(() => {
        this.loader.stopLoading();
      }, 2000);
      // fetch notes by category
      this.sermonsService.getCategoryNotes(url, this.page_id).subscribe((res: any) => {

        // ======= parsing xml into JsSON format =============================
        // let parseString = require('xml2js').parseString;
        // parseString(res, function (err, result) {
        //   self.allNotes = result.rss.channel[0].item;
        //   self.loader.stopLoading();
        // });
        // ======= parsing xml into JsSON format =============================

        self.allNotes = res.result.channel.item;
          self.allNotes.map((res)=>{
            if(res.description && JSON.stringify(res.description) != '{}') {
              if (res.description.includes("<img")) {
                res.image_url = (res.description.match(/src=(.+?[\.jpg|\.gif|\.png]")/)[1]).slice(1, -1);
                if (res.image_url.includes("150x150")) {
                  if (res.image_url.includes("-150x150")) {
                    res.image_url = res.image_url.replace("-150x150", '');
                  } else {
                    res.image_url = res.image_url.replace("150x150", '');
                  }
                } else if (res.image_url.includes("300x300")) {
                  if (res.image_url.includes("-300x300")) {
                    res.image_url = res.image_url.replace("-300x300", '');
                  } else {
                    res.image_url = res.image_url.replace("300x300", '');
                  }
                }

                if( res.image_url !== "" && res.image_url !== null && res.image_url.includes("s3.wasabisys.com") && formatDate(res.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29' ){

          
                  let abc =res.image_url.split("/");
                  //  let img_name = abc[abc.length-1];
                    let folder_name = abc[abc.length-2];
                  if(this.platform.is("mobile")){
                      this.deviceType = 'radiobase_mobile';
                  }else if(this.platform.is("tablet")){
                      this.deviceType = 'radiobase_tablet';
                  }else{
                      this.deviceType = folder_name;
                  }

                  let new_link = res.image_url.replace(folder_name, this.deviceType);
                  res.image_url = new_link; 
                 // console.log('background is: ',card.background,'new link is: ', new_link);
              }
              }
            }
          });
        self.loader.stopLoading();
      }, (err: any) => {
        let message = err.message;
        self.loader.stopLoading();
        this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
      });
    });
  }

  openDetail(i) {
    let md5 = require('md5');
    let hashed_date;
    hashed_date = this.allNotes[i].pubDate ? md5(this.allNotes[i].pubDate) : '';     // hashed Date before sending
    let hashed_title = this.allNotes[i].title ? md5(this.allNotes[i].title) : '';    // hashed title before sending
    if(!this.is_wordpress_enable){
      this.router.navigate(['custom-post-detail', {
        title: hashed_title,
        pubDate: hashed_date,
        url: this.is_external_url ? this.post_url : this.post_category_url,
        is_external: this.is_external_url,
        page_id: this.page_id,
        lc: this.is_wordpress_enable
      }]);
    } else {
      this.router.navigate(['custom-post-detail', {
        ch: this.theme.ch_id,
        id: this.allNotes[i].id,
        page_id: this.page_id,
        lc: this.is_wordpress_enable
      }]);
    }
  }

  gotobackPage() {
    this.location.back();
  }

  getUser() {
    // getting user from local storage
    this.storage.get('user').then((val: any) => {
      if (val != null && val != 'guest') {
        this.user = val;
      }
    });
  }

  setAnalytics() {
    let dev_id = '';
    let device = 'web';
    // preparing device data
      dev_id = this.device.uuid;
      if (dev_id){
        device =  this.platform.platforms()[0];
      }

    // preparing duration data
    let end_time_analytics = moment();
    var duration:any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

    // preparing user data
    let userId = this.user ? this.user.user.id : 'guest';

    // preparing data for analytics
    const body = JSON.stringify({
      church_id: this.theme.ch_id,
      latitude: this.theme.latitude ? this.theme.latitude : "",
      longitude: this.theme.longitude ? this.theme.longitude : "",
      timespent: duration,
      module: this.page_name,
      custom_name: this.custom_name,
      ip_address: "",
      device: device,
      device_id: dev_id,
      client_type: userId,
      event_type: ""
    });
    // calling function from service to update Analytics Data
    this.analytics.setAnalytics(body).subscribe((res: any) => {

    })
  }

  ionViewDidLeave(){
    this.setAnalytics();
  }
  ionViewDidEnter(){
    // setting start time for analytics
    this.start_time_analytics = moment();
    this.event_provider.hidefooter(true, 'other');
  }

  ionViewWillEnter() {
    this.ngOnInit();
  
    this.event_provider.getChurchData.subscribe(async (res: any) => {
        if (!(Object.keys(res).length === 0)) {
            // asking for password
            var id  = this.route.snapshot.paramMap.get('page_id');
            let app_pages = res.apppages.find(x => x.id === id);
            if (app_pages && app_pages.password_protection_toggle === 'true') {
                this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
            }
        }
    });
  
  }


  
}
